*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background-color: #f5f5f5;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  padding: 20px;
  font-family: Inter, sans-serif;
  line-height: 1.6;
  display: flex;
}

button {
  color: #fff;
  cursor: pointer;
  background: #222;
  border: none;
  border-radius: 8px;
  padding: 12px 24px;
  font-size: 16px;
  transition: background .3s;
}

button:hover {
  background: #555;
}

input {
  border: 1px solid #ccc;
  border-radius: 6px;
  width: 100%;
  max-width: 300px;
  padding: 10px;
  font-size: 16px;
}

img {
  border-radius: 8px;
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  flex-direction: column;
  align-items: center;
  gap: 20px;
  display: flex;
}

.list {
  flex-direction: column;
  gap: 10px;
  list-style: none;
  display: flex;
}

.list-item {
  text-align: center;
  background: #fff;
  border-radius: 6px;
  width: 100%;
  max-width: 300px;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}
